import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_ENABLE_AUTOFIT_WIDTH = Symbol('TABLE_ENABLE_AUTOFIT_WIDTH');

export function setTableAutofitWidthEnable(table: Table, containerWidth?: number) {
  return {
    type: TABLE_ENABLE_AUTOFIT_WIDTH,
    payload: {
      node: table,
      containerWidth,
    },
  };
}

interface TablesetTableAutofitWidthEnablePayload {
  node: Table;
  containerWidth?: number;
}

interface TablesetTableAutofitWidthEnableAction {
  type: typeof TABLE_ENABLE_AUTOFIT_WIDTH;
  payload: TablesetTableAutofitWidthEnablePayload;
}

export function isTablesetTableAutofitWidthEnableAction(
  action: Action,
): action is TablesetTableAutofitWidthEnableAction {
  return action.type === TABLE_ENABLE_AUTOFIT_WIDTH;
}
